import React, {useState} from "react"
import '../styles/normalize.css'
import Header from "../components/header"
import 'bootstrap/dist/css/bootstrap.min.css'
import About from '../components/about'
import axios from 'axios'
import css from '../styles/about.module.scss'
import code from '../images/code.svg'
import Icons from '../components/icon'
import { Link } from "gatsby"
import Email from '../components/email.js'

const IndexPage = () =>  {
  let [resume, setResume] = useState("")
  let [quote, setQuote] = useState("")
  let [contact, setContact] = useState(false)
  let getKanye = ()=>{
    axios.get('https://api.kanye.rest/').then(data=>{
      setQuote(data.data.quote)
    })
  }
    let getResume = ()=>{
      axios.get('https://resumes.free.beeceptor.com').then((data)=>{
        setResume(data.data[Math.floor(Math.random()*19)])
    })
    }
  return (<div className={css.about}>
    <Header/>
    {contact ? <Email foo={setContact} /> : null}
    {quote ? <p className={css.qoute}>"{quote}"</p> : null}

    <div className={css.intro}>
    <p style={{margin:'0'}}>Hi! I'm hatr1ck.</p>
    <p>Developer. Thinker. Caffeine addict.</p>
    <p style={{margin:'0'}}>This website belongs to a wanna be webdev.  It's basically a place where some guy putting hes projects to share them with others.
      Some of <Link style={{textDecoration: "none", color:'white', margin: '0px'}} className={css.feature}  to="/works/">stuff</Link> that I have for now wasn't really created with intention to see the outer world, so it have some bugs here and there, but I'm looking forward to grow and turn my hobby into job.
      If you have some project in mind or just want to chat, send me a message through this <span className={css.feature} style={{margin:'0px'}} onClick={()=>{setContact(!contact)}}>form</span></p>
      <p style={{margin:'5px'}} > If this about page dosen't feel complete, you can use a couple of mine best creations:</p>
    <div className={css.feature} onClick={getResume}>
      Resume filler API
    </div>
    <div className={css.feature} onClick={getKanye}>
      Kanye Rest API
    </div>
    {resume ? <p className={css.resume}>{resume}</p> : null}
    </div>
    {/* <p className={css.teckStack}>My teck stack:</p> */}
    <img className={css.computer} width="80%" src={code} />

    <div className={css.icons}>
    <Icons style={{bottom:'0px'}} />
    </div>
  </div>
)}

export default IndexPage
